import EventManager from "../../../eventManager/EventManager";
import { GameEvents } from "../../../gameEvents/GameEvents";
import { BaseState } from "../../../state-machine/BaseState";

export class FinishState extends BaseState {

  begin(data?: any) {
    EventManager.dispatch(GameEvents.FINISH_START);
    EventManager.add(GameEvents.FINISH_END, this.end);
  }

  end = (): void => {
  }

  cleanUp(): void {

  }
}